* {
  font-family: Arial, Helvetica, sans-serif;
}

html {
  height: 100%;
}

body {
  background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRUuH1j3ZTEgOmV935PaXANVZR7nLwYOASuboJfAdxD&s");
  background-size: cover;
  background-position: center;
}

.calculator--container {
  background-color: white;
  max-width: 600px;
  margin: 10px auto;
  border: 2px solid black;
  border-radius: 20px;
}

.form {
  padding: 10px;
}

.form__fieldset {
  border-radius: 20px;
}

.display {
  text-align: center;
  font-weight: 700;
  font-size: 40px;
  border: dashed black 2px;
  border-radius: 15px;
  max-width: 40%;
  margin: 0px auto;
  min-width: 230px;
}

.button {
  background-color: teal;
  border: none;
  text-align: center;
  border-radius: 10px;
  font-size: 20px;
  color: white;
  padding: 5px;
  border: solid black;;
}

.button:hover {
  background-color: rgb(0, 255, 255);
  color: black;
}

.button:active {
  background-color: gray;
}

.button__section {
  text-align: center;
}